import React, { Component , Fragment } from "react";
import ScrollToTop from 'react-scroll-up';
import { FiChevronUp } from "react-icons/fi";
import Header from "../component/header/Header";
import FooterTwo from "../component/footer/FooterTwo";
import Helmet from "../component/common/Helmet";

const SlideList = [
    {
        textPosition: 'text-center',
        category: '',
        title: 'test',
        description: 'A fresh take on your Sunday Funday. Run by the community, for the community',
        buttonText: 'Contact Us',
        buttonLink: 'mailto:contact@sundaygays.com'
    }
]

class HomePortfolio extends Component {
    constructor () {
        super()
        this.state = {
            isOpen: false
        }
        this.openModal = this.openModal.bind(this)
    }
    openModal () {
        this.setState({isOpen: true})
    }
    render(){
        return(
            <div className="active-dark bg_color--10"> 
                <Helmet pageTitle="Sunday Gays" />

                {/* Start Header Area  */}
                <Header />
                {/* End Header Area  */}
                {/* Start Slider Area   */}
                <div className="slider-wrapper">
                    {/* Start Single Slide */}
                    {SlideList.map((value , index) => (
                        <div className="slide slide-style-2 fullscreen d-flex align-items-center justify-content-center bg_image bg_image--24" key={index} data-black-overlay="6">
                            <div className="container">
                                <div className="row">
                                    <div className="col-lg-12">
                                        <div classname="image-sizing"><center><img class="image-sizing" src="assets/images/tea/mark+tag-removebg.png"></img></center></div>
                                        <div className={`inner ${value.textPosition}`}>
                                            <br></br>
                                            <p className="description"><b>SUNDAY GAYS</b><p></p>A fresh take on your Sunday Funday. <p></p> Run by the community, for the community</p><p></p><p>Coming Soon...</p>
                                            {value.buttonText ? <div className="slide-btn"><a className="btn-default btn-border btn-opacity" href={`${value.buttonLink}`}>{value.buttonText}</a></div> : ''}
                                            <br></br><br></br><br></br><br></br><br></br><br></br><br></br>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    ))}
                    {/* End Single Slide */}
                </div>
                {/* End Slider Area   */}
        

                {/* Start Footer Area  */}
                {/* <FooterTwo /> */}
                {/* End Footer Area  */}
                {/* Start Back To Top */}
                {/* <div className="backto-top">
                    <ScrollToTop showUnder={160}>
                        <FiChevronUp />
                    </ScrollToTop>
                </div> */}
                {/* End Back To Top */}
            </div>
        )
    }
}
export default HomePortfolio;